import React from 'react';
import { t } from '../../i18n';
import * as DateUtils from '../../utils/DateUtils';
import FormatsConfiguration from '../../utils/FormatsConfiguration';
import { styles } from './ExcelTemplate.styles';

const OperationsTemplateExcel = props => {
  let { selectedAccountNumber, fullName } = props;
  let renderOperations = props.tableRows.map(row => (
    <tr style={styles.table}>
      <td style={styles.firstColomn}>
        <p style={styles.firstColomnValue}>
          {DateUtils.formatDateTime(
            row.date,
            FormatsConfiguration.DateTimeFormat.fullDate,
          )}
        </p>
      </td>
      <td colSpan="2" style={styles.firstColomn}>
        <p style={styles.firstColomnValue}>
          {DateUtils.formatDateTime(row.dateValue)}
        </p>
      </td>
      <td colSpan="2" style={styles.firstColomn}>
        <p style={styles.firstColomnValue}>{row.code}</p>
      </td>
      <td colSpan="2" style={styles.firstColomn}>
        <p style={styles.firstColomnValue}>
          {row.sign === 'D' ? row.amount * -1 : row.amount}
        </p>
      </td>
      <td colSpan="6" style={styles.firstColomn}>
        <p style={styles.firstColomnValue}>{row.description}</p>
      </td>
    </tr>
  ));

  return (
    <div id="Excel" style={styles.pdf}>
      <div id="id1_1" style={styles.titleContainer}>
        <p style={styles.title}>{t('Accounts:operationsHistory')}</p>
      </div>
      <div id="id1_2" style={styles.header}>
        <div style={styles.clientInfosContainer}>
          <p style={styles.clientInfos}>
            {t('Accounts:client')} {fullName} <br />
            {t('Accounts:accountNumber')}
            {selectedAccountNumber}
          </p>
        </div>
        <div style={styles.operationsPeriodeContainer} />
      </div>
      <div style={styles.operations}>
        <div style={styles.operationsTableContainer}>
          <table cellPadding="0" cellSpacing="0" style={styles.table}>
            <thead>
              <tr>
                <td style={styles.dateOperationContainer}>
                  <p style={styles.dateOperation}>{t('Accounts:date')}</p>
                </td>
                <td colSpan="2" style={styles.dateValueContainer}>
                  <p style={styles.dateValue}>{t('Accounts:dateValue')}</p>
                </td>
                <td colSpan="2" style={styles.referenceContainer}>
                  <p style={styles.reference}>{t('Accounts:reference')}</p>
                </td>
                <td colSpan="2" style={styles.amountContainer}>
                  <p style={styles.amount}>
                    {t('Accounts:amount', {
                      currency: t('Components:currency'),
                    })}
                  </p>
                </td>
                <td colSpan="6  " style={styles.labelContainer}>
                  <p style={styles.label}>{t('Accounts:description')}</p>
                </td>
              </tr>
            </thead>
            <tbody>{renderOperations}</tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default OperationsTemplateExcel;
